import React, { useRef, useEffect } from "react";

/**
 * FlyingBirdLogo
 * Renders the AVASA bird logo (from SVG), with a transparent background and a simple "flying" animation.
 * The bird gently flaps its wings and moves up and down.
 */
const FlyingBirdLogo = ({
  width = 200,
  height = 200,
  style = {},
  className = "",
}) => {
  const birdRef = useRef(null);

  useEffect(() => {
    let frame = 0;
    let raf;
    const animate = () => {
      // Flap wings: oscillate between -20deg and +20deg
      const wingAngle = Math.sin(frame * 0.15) * 20;
      // Bob up and down: oscillate Y position
      const y = Math.sin(frame * 0.07) * 10;
      // Slight tilt for realism
      const tilt = Math.sin(frame * 0.09) * 5;

      if (birdRef.current) {
        birdRef.current.setAttribute(
          "transform",
          `translate(0,${y}) rotate(${tilt},300,300)`
        );
        // Animate wings (left and right)
        // Use querySelector only if birdRef.current is an SVGElement
        const leftWing = birdRef.current.querySelector
          ? birdRef.current.querySelector("#leftWing")
          : null;
        const rightWing = birdRef.current.querySelector
          ? birdRef.current.querySelector("#rightWing")
          : null;
        if (leftWing) {
          leftWing.setAttribute("transform", `rotate(${-wingAngle},180,340)`);
        }
        if (rightWing) {
          rightWing.setAttribute("transform", `rotate(${wingAngle},420,340)`);
        }
      }
      frame++;
      raf = requestAnimationFrame(animate);
    };
    raf = requestAnimationFrame(animate);
    return () => raf && cancelAnimationFrame(raf);
  }, []);

  return (
    <svg
      width={width}
      height={height}
      viewBox="0 0 600 600"
      style={{ display: "block", background: "none", ...style }}
      className={className}
      xmlns="http://www.w3.org/2000/svg"
    >
      {/* The main bird group, animated */}
      <g ref={birdRef}>
        {/* Body */}
        <ellipse
          cx="300"
          cy="350"
          rx="70"
          ry="90"
          fill="#111"
          stroke="#111"
          strokeWidth="2"
        />
        {/* Head */}
        <ellipse
          cx="300"
          cy="250"
          rx="40"
          ry="40"
          fill="#111"
          stroke="#111"
          strokeWidth="2"
        />
        {/* Beak */}
        <polygon
          points="300,220 310,240 290,240"
          fill="#d4a14a"
          stroke="#b88b2e"
          strokeWidth="2"
        />
        {/* Left Wing */}
        <g id="leftWing">
          <path
            d="M230,370 Q120,320 200,250 Q220,320 230,370"
            fill="#111"
            stroke="#111"
            strokeWidth="2"
          />
        </g>
        {/* Right Wing */}
        <g id="rightWing">
          <path
            d="M370,370 Q480,320 400,250 Q380,320 370,370"
            fill="#111"
            stroke="#111"
            strokeWidth="2"
          />
        </g>
        {/* Tail */}
        <polygon
          points="300,440 320,500 280,500"
          fill="#111"
          stroke="#111"
          strokeWidth="2"
        />
        {/* Eye */}
        <circle cx="315" cy="245" r="6" fill="#fff" />
        <circle cx="317" cy="247" r="2" fill="#111" />
      </g>
    </svg>
  );
};

export default FlyingBirdLogo;
